.dialog-meta-date {
    font-size: 12px;
    color: #70777b;
    white-space: nowrap;
    margin: 1px 0 -1px 6px;
}

.dialog-meta-date-active {
    color: #fff;
}

.dialog-active .dialog-meta-date {
    color: #fff;
}

.dialog-meta-status {
    font-size: 15px;
    margin: 3px 0 0;
}

.dialog-meta-status svg {
    display: block;
}
